body {
  font-family: "Montserrat", sans-serif;
  background: url(/github-app/static/media/space2.fa3e3e78.jpg) 50% 50% no-repeat;
  background-size: cover;
}
.info {
  right: 0;
  font-size: 1.2em;
  color: #fff;
  opacity: 0.3;
  overflow-y: auto;
  height: 100vh;
  padding-right: 8px;
}
.info ul {
  list-style: none;
}
.info input {
  border-radius: 10px;
  height: 24px;
  padding: 0 8px;
}
.info button {
  border-radius: 10px;
  font-weight: 900;
  line-height: 24px;
  margin: 0 0 0 8px;
}
.infoAcc {
  position: absolute;
  left: 16px;
  bottom: 16px;
}
.infoAcc img {
  width: 48px;
  height: 48px;
}
.infoAcc span {
  font-size: 24px;
  color: #fff;
  padding: 8px;
}

